
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    title: {
      type: String,
      required: true
    },
    dataList: {
      type: Array,
      required: true
    },
    propList: {
      type: Array,
      required: true
    },
    showSelectColumn: {
      type: Boolean,
      default: false
    },
    listCount: {
      type: Number,
      default: 0
    },
    page: {
      type: Object,
      default: () => ({ currentPage: 0, pageSizes: 10 })
    },
    childrenProps: {
      type: Object,
      default: () => ({})
    },
    showPagination: {
      type: Boolean,
      default: true
    }
  },
  emits: ['update:page'],
  setup(props, { emit }) {
    // console.log('dataList:', props.dataList)

    //切换页数
    const handleSizeChange = (pageSize: number) => {
      console.log('改变页数')
      emit('update:page', { ...props.page, pageSize })
    }
    //跳转页数
    const handleCurrentChange = (currentPage: number) => {
      // console.log('111:', currentPage)
      //每一页减1为offset
      currentPage = currentPage - 1
      console.log('111:', currentPage)
      emit('update:page', { ...props.page, currentPage })
    }
    return {
      handleSizeChange,
      handleCurrentChange
    }
  }
})
