
import { defineComponent, computed, ref, watch, reactive } from 'vue'
import { useStore } from '@/store'
import HyTable from '@/base-ui/table/src/table.vue'
import { usePermission } from '@/hooks/use-permission'

export default defineComponent({
  components: {
    HyTable
  },
  props: {
    departmentItemName: {
      type: Object,
      default: () => ({})
    },
    roleItemName: {
      type: Object,
      default: () => ({})
    },
    contentTableConfig: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    },
    headName: {
      type: String,
      default: ''
    }
  },
  emits: ['handleNewClick', 'handleEditClick'],
  setup(props, { emit }) {
    //获取按钮的权限
    const isCreate = usePermission(props.pageName, 'create')
    const isUpdate = usePermission(props.pageName, 'update')
    const isDelete = usePermission(props.pageName, 'delete')
    const isQuery = usePermission(props.pageName, 'query')
    // console.log(isCreate, isUpdate, isDelete, isQuery)

    //1、双向绑定pageInfos
    const pageInfo = ref({ currentPage: 0, pageSize: 10 })
    //2、监听pageInfo
    //需要深度监听
    watch(
      () => pageInfo,
      () => {
        getPageData()
      },
      { deep: true }
    )

    //3、发送网络请求
    const store = useStore()
    const getPageData = (queryInfo: any = {}) => {
      // if (!isQuery) return
      console.log(pageInfo.value.currentPage * pageInfo.value.pageSize)
      store.dispatch('system/getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          //查询偏移量
          offset: pageInfo.value.currentPage * pageInfo.value.pageSize,
          //查询的数据条数
          size: pageInfo.value.pageSize,
          //根据搜索条件模糊查询
          ...queryInfo
        }
      })
    }
    getPageData()
    // const departmentNameList = reactive(props.departmentItemName)
    // console.log('传值departmentItemName:', props.departmentItemName)
    // const roleNameList = reactive(props.roleItemName)
    // console.log('传值roleItemName:', props.roleItemName)

    //4、删除//编辑//新建
    //删除
    const handleDeleteClick = (item: any) => {
      // console.log(item)
      store.dispatch('system/deletePageListAction', {
        pageName: props.pageName,
        id: item.id
      })
    }

    //新建
    const handleNewClick = () => {
      emit('handleNewClick')
    }

    //编辑
    const handleEditClick = (item: any) => {
      // console.log(item)
      emit('handleEditClick', item)
    }

    //获取vuex数据
    const dataList = computed(() =>
      store.getters[`system/pageListData`](props.pageName)
    )
    // console.log('dataList:', dataList.value)
    const listCount = computed(() =>
      store.getters[`system/pageListCount`](props.pageName)
    )

    return {
      dataList,
      listCount,
      pageInfo,
      getPageData,
      handleDeleteClick,
      handleNewClick,
      handleEditClick,
      isCreate,
      isUpdate,
      isDelete
    }
  }
})
